button,
select,
html,
textarea,
input {
  font-family: 'Gorgia';
}
.md-toolbar-tools {
  font-size: 16px;
}
.md-blueframe-z1 {
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 255, 0.14), 0px 2px 2px 0px rgba(0, 0, 255, 0.098), 0px 1px 5px 0px rgba(0, 0, 255, 0.084);
}
.md-button {
  border-radius: 20px;
  padding: 0 10px;
  text-transform: none;
}
.md-button.md-min-width {
  min-width: 180px;
}
md-input-container.big {
  font-size: 1.5em;
}
md-autocomplete[md-floating-label] {
  padding: 0 !important;
}
md-dialog {
  padding: 24px;
}
md-dialog.md-color-picker-dialog {
  padding: 0px;
}
md-dialog.md-color-picker-dialog .md-color-picker-colors md-tabs {
  min-height: 298px !important;
  padding: 0px;
}
md-dialog.md-color-picker-dialog .md-color-picker-sliders > div {
  width: 100%;
}
md-toast {
  position: fixed;
}
md-list {
  padding: 0px;
}
md-list md-list-item .md-button ng-md-icon {
  position: static;
  top: 0;
}
md-list.no-line md-list-item {
  border-bottom: 0px;
  position: relative;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  transition: all 0.2s ease-in-out;
  transform: scale(1);
  /*  &::after, &::before{
        content:'';
        height:1px;
        width:0;
        left:-2em;
        position:absolute;
        background-color: @grey-light;
        transition: all .35s ease-in-out;
      }
      &::after{
        bottom:0;
      }
      &::before{
        top:0;
      }*/
  /*&.checked{
        background-color:@base;
        color:#fff;
      }*/
}
md-list.no-line md-list-item:hover {
  /*&::after, &::before{
          width:150%;
        }*/
}
a.md-button ng-md-icon {
  position: relative;
  top: 0.5em;
}
.md-button .md-ripple-container {
  border-radius: 20px;
}
.md-button.small {
  min-width: 0px;
  min-height: 0px;
}
.md-button.md-icon-button.small {
  min-width: 0px;
  min-height: 0px;
  width: 24px;
  height: 24px;
  line-height: 24px;
}
.md-button.md-icon-button.small ng-md-icon {
  top: 5px;
}
.md-button.md-lowspace {
  padding: 0 10px;
}
.md-button.md-border {
  border-width: 1px !important;
}
.md-button.md-border:focus {
  border-width: 1px;
}
.md-button.md-fab.md-mini .material-icons {
  margin-left: -2px;
  margin-top: -2px;
}
a.md-button span {
  margin: 0;
  margin-left: 5px;
}
md-list-item {
  color: #333333;
}
md-list-item path {
  fill: #afafaf;
}
md-list-item .md-button {
  border-radius: 0;
}
md-list-item .md-button .md-ripple-container {
  border-radius: 0;
}
.md-border {
  border: 1px solid !important;
}
.md-border.md-focused {
  border: 1px solid;
}
md-input-container.md-has-icon {
  padding-left: 36px;
}
md-input-container ng-md-icon {
  position: absolute;
  top: 5px;
  left: 2px;
}
md-menu-content {
  padding: 0px;
}
.md-open-menu-container md-menu-divider {
  margin: 0;
}
ng-md-icon path {
  fill: #006bb9;
}
ng-md-icon.no-color path {
  fill: #d7dddd;
}
md-checkbox.no-label .md-label {
  margin-left: 0px;
}
md-checkbox.no-space {
  margin: 0px;
  padding: 0px;
}
md-toolbar.clean {
  color: #afafaf !important;
  background-color: #fff !important;
  padding-top: 1em;
}
md-toolbar.clean .md-toolbar-tools {
  font-size: 1.2em;
}
.info.success,
p.success,
span.success {
  color: #00ccaa;
}
.info.success path,
p.success path,
span.success path {
  fill: #00ccaa;
}
.info.warning,
p.warning,
span.warning {
  color: #f44336;
}
.info.warning path,
p.warning path,
span.warning path {
  fill: #f44336;
}
.mini-info {
  max-width: 305px;
  text-align: justify;
  color: #aaa;
  font-size: 0.9em;
}
.md-warn ng-md-icon path {
  fill: #f44336;
}
md-sidenav {
  box-sizing: border-box;
  position: absolute;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  z-index: 60;
  width: 370px;
  min-width: 370px;
  max-width: 370px;
  bottom: 0;
  overflow: auto;
}
md-sidenav.md-locked-open {
  width: 370px;
  min-width: 370px;
  max-width: 370px;
}
.md-subheader .md-subheader-inner {
  font-size: 20px;
  line-height: 1.1em;
}
.md-top {
  position: absolute;
  top: 0px;
  width: 100%;
  z-index: 20;
  padding: 0 1em;
  background-color: #fff;
  height: 100%;
}
.md-top md-input-container .md-errors-spacer {
  position: absolute;
}
.md-subheader-wrapper:not(.md-sticky-no-effect).md-sticky-clone {
  z-index: 21;
}
md-bottom-sheet {
  position: fixed;
}
md-tooltip {
  font-size: 0.7em;
  font-family: 'Roboto';
}
.noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE/Edge */
  user-select: none;
  /* non-prefixed version, currently
                                  not supported by any browser */
}
