div[ui-view='map'] {
  position: absolute;
  width: 100%;
  top: 0px;
  height: 100%;
}
#map {
  position: absolute;
  width: 100%;
  top: 0px;
  left: 0px;
  height: 100%;
  transition: all .6s ease;
}
.angular-leaflet-map {
  width: 100%;
  height: 100%;
  background-color: #ccdddd;
}
.greyed .angular-leaflet-map {
  background-color: #d9d9d9;
}
.greyed img.leaflet-tile {
  transition: filter 0.5s ease-in-out;
  filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -o-filter: grayscale(1);
  -ms-filter: grayscale(1);
}
.country_info {
  background-color: #fff;
  padding: 00px;
  padding-right: 00px;
  position: fixed;
  bottom: 20px;
  right: 00px;
}
.label-icon-number {
  background-color: #6eafff;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 33px;
  box-shadow: 0px 0px 0px 5px rgba(255, 255, 255, 0.3);
}
.label-icon-number-lg {
  background-color: #6eafff;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 41px;
  box-shadow: 0px 0px 0px 5px rgba(255, 255, 255, 0.3);
}
.label-icon-text {
  display: inline-block;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  vertical-align: middle;
  text-shadow: 0px 1px 1px #232323;
  color: #ebf9f5;
}
