*:focus {
  border: 0;
  outline: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
body {
  padding-bottom: 0 !important;
  font-family: 'Gorgia';
  font-size: 16px;
  color: #606060;
  overflow-x: hidden;
  background: url("/images/geometry.png");
}
body > canvas {
  z-index: 1000;
  position: fixed;
  right: 10px;
  bottom: 10px;
}
body > md-content {
  border-top: 1px solid rgba(0, 107, 185, 0.4);
}
.Page-name {
  color: #006bb9;
}
.logo {
  color: #006bb9;
  font-size: 1.5em;
}
.info {
  color: #006bb9;
}
.info path {
  fill: #006bb9;
}
.icon-space {
  position: relative;
  top: 4px;
  left: 5px;
}
.inline-list li {
  display: inline;
}
.inline-list.menu-list li {
  position: relative;
  padding: 0 1em;
}
.inline-list.menu-list li::before {
  content: "/";
  position: absolute;
  right: 0;
  top: 0;
}
.inline-list.menu-list li:last-child::before {
  content: "";
}
.inline-list.menu-list li.active a {
  color: #006bb9;
}
.inline-list.menu-list li a {
  position: relative;
  color: #333333;
}
.inline-list.menu-list li a::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 0;
  left: 50%;
  bottom: -5px;
  border-bottom: 1px solid #006bb9;
  transition: all 0.2s ease;
}
.inline-list.menu-list li a:hover::after {
  width: 100%;
  left: 0;
}
.icon-bubble {
  background-color: #d7dddd;
  color: #fff;
  border-radius: 50%;
  font-size: 2em;
}
.icon-bubble.fisheries {
  background-color: #008c8c;
}
.icon-bubble.healthimpact {
  background-color: #ff9600;
}
.icon-bubble.airquality {
  background-color: #f7c80b;
}
.icon-bubble.watersanitation {
  background-color: #ff6d24;
}
.icon-bubble.waterresources {
  background-color: #7993f2;
}
.icon-bubble.agriculture {
  background-color: #009bcc;
}
.icon-bubble.forest {
  background-color: #2e74ba;
}
.icon-bubble.biodiversity {
  background-color: #00ccaa;
}
.icon-bubble.climate {
  background-color: #1cb85d;
}
p.small-info {
  color: #aaa;
  font-size: 0.9em;
}
p.small-info:hover {
  color: #006bb9;
}
.rowed #map {
  left: 400px;
  width: calc(100% - 400px);
}
.rowed .Page {
  width: 400px;
  height: 100%;
}
.loose .Header {
  background: #fff !important;
}
.loose .Page {
  margin-left: calc(370px + 2em);
  margin-top: 1em;
  margin-right: 1em;
  transition: margin-left 0.25s ease-in-out;
  transition-delay: 0s;
  width: auto;
  position: absolute;
  max-height: calc(100% - 2em);
  max-width: calc(100% - 370px - 3em);
}
.loose .Page section.table {
  height: calc(100% - 2em);
}
.loose .Sidebar {
  position: absolute !important;
  top: 1em;
  left: 1em;
  bottom: auto;
  max-height: calc(100% - 2em);
  overflow-x: hidden;
}
.loose.sidebar-closed .Page {
  margin-left: 1em;
  transition-delay: .1s;
}
.loose #map {
  left: 0px;
  width: 100%;
}
.loose.rowed .Page {
  width: 400px;
}
.additional {
  position: absolute;
  top: 0;
  width: 400px;
  height: 100%;
  left: 400px;
  background-color: #fff;
}
