.Header {
  height: 48px;
  min-height: 48px;
  background-color: #fff !important;
}
.Header .md-toolbar-tools,
.Header md-toolbar {
  height: 48px;
  min-height: 48px;
}
.Header a {
  color: #006bb9;
}
.Header a.md-button span {
  color: #006bb9;
}
.Header .md-menu {
  padding: 0px;
}
.Header .inline-list.menu-list li a,
.Header .inline-list.menu-list li::before {
  color: #006bb9;
}
md-toolbar.Header .md-button.md-border {
  color: #006bb9;
}
